import Vue from "vue";
import App from "./App.vue";
import vuetify from "./plugins/vuetify";
import drag from "v-drag";
import VueCarousel from "vue-carousel";
import Particles from "vue2-particles";

Vue.config.productionTip = false;
Vue.use(drag);
Vue.use(VueCarousel);
Vue.use(Particles);

new Vue({
  vuetify,
  render: (h) => h(App),
}).$mount("#app");
