<template>
  <v-app class="app">
    <v-main>
      <IndexScreen ref="IndexScreen" />
    </v-main>
  </v-app>
</template>

<script>
import IndexScreen from "./components/IndexScreen";
import ArtGalleryModal from "./components/ArtGalleryModal";
import { blogAndIdMapping } from "./assets/blogs/blogAndIdMapping";
import Vue from "vue";

export default {
  name: "App",
  components: {
    IndexScreen,
  },
  data() {
    return {
      urlParams: undefined,
    };
  },
  methods: {
    redirectToBlogsModalIfUrlContainsBlogTitle: function () {
      if (this.urlParams.has("blog")) {
        const blogTitle = this.urlParams.get("blog");
        const blogId = blogAndIdMapping[blogTitle];
        if (blogId) {
          this.$refs.IndexScreen.isHomeOpen = false;
          setTimeout(() => {
            this.$refs.IndexScreen.isBlogsOpen = true;
            this.$refs.IndexScreen.$refs.blogsModal.openedBlogId = blogId;
          }, 100);
        }
      }
    },
  },
  mounted() {
    this.urlParams = new URLSearchParams(window.location.search);
    // invokes art gallery modal
    const artGalleryModal = Vue.extend(ArtGalleryModal);
    new artGalleryModal();

    // redirect to blogs modal and open the specified blog in the URL.
    // this.redirectToBlogsModalIfUrlContainsBlogTitle();
  },
};
</script>

<style lang="scss">
.app {
  background-color: #DFCCFB !important;
  color: #31302c !important;
  font-family: Jost, sans-serif;
  font-size: 15px;
  margin: 0;
  height: 100%;
  background-size: 40px 40px !important;
  background-image: linear-gradient(90deg, #a2bfae 1px, transparent 0),
    linear-gradient(180deg, #a2bfae 1px, transparent 0) !important;
  overflow: hidden;
  background-repeat: repeat;
}

@font-face {
  font-family: "Annie Telescope";
  src: url("./AnnieUseYourTelescope.ttf") format("truetype");
}

@font-face {
  font-family: "Indie Flower";
  src: url("./IndieFlower.ttf") format("truetype");
}
</style>
